body {
  background: -webkit-linear-gradient(#336699, #335599);
  background: linear-gradient(#336699, #335599);
  color: ivory;
  min-height: 100vh;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

button {
  background: rgba(0, 0, 0, 0.6);
  border: none;
  color: ivory;
  cursor: pointer;
  font-size: 2em;
  outline: none;
  min-width: 100px;
  min-height: 75px; }

button:hover {
  background: rgba(0, 0, 0, 0.3) !important; }

button:active {
  color: lightgreen;
  background: black !important; }

hr {
  border: none;
  border-top: 1px solid ivory;
  opacity: 0.3;
  width: 100%; }

#Calculator {
  background: #336699;
  border: 1px solid ivory;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.35);
  font-family: "Concert One", monospace;
  padding: 10px; }

#header {
  background: rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  text-align: right;
  padding: 5px;
  width: 100%;
  min-height: 40px; }

#history {
  font-size: 1.5em;
  max-width: 380px;
  min-height: 20px; }

#display {
  font-size: 3em; }

#buttons {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-template-rows: repeat(5, auto);
  grid-column-gap: 2px;
  -webkit-column-gap: 2px;
          column-gap: 2px;
  grid-row-gap: 2px;
  row-gap: 2px;
  grid-template-areas: "clear clear clear divide" "seven eight nine multiply" "four five six subtract" "one two three add" "zero zero decimal equals"; }

#App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; }

footer {
  background: #336699;
  font-family: "Indie Flower", cursive;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px; }
  footer a {
    color: inherit;
    text-decoration: none;
    -webkit-transition: 1s color;
    transition: 1s color; }
    footer a:hover {
      color: lightgreen; }

@media (max-height: 650px) {
  #App {
    flex-direction: column;
    justify-content: space-between; }
  footer {
    position: static;
    position: initial; } }

